:root {
  --white: #ffffff;
  --rich-black: #013243;
  --black: #000000;
  --cyan: #86bfd3;
}

:root {
  --font-roboto: "Roboto", sans-serif;
}

body {
  font-family: var(--font-roboto);
}

body {
  display: flex;
  flex-direction: column;
}
body main {
  display: flex;
  flex-direction: row;
  height: 50vh;
}
@media (max-width: 480px) {
  body main {
    flex-direction: column;
    height: 50%;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh;
  font-family: var(--font-roboto);
  background-image: url("/images/Background.png");
}
@media screen and (max-width: 360px) {
  body {
    background: none;
  }
}

.phone {
  background-color: var(--rich-black);
}
.phone__header {
  color: var(--white);
}
.phone__header .htitle {
  font-size: 3rem;
  font-weight: bold;
}
.phone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.phone__header {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.phone__image img {
  height: 40vh;
  width: 80vw;
}